@import "core";
@import "config";
@import "general";

.intro-header {
	padding: 0 0 2rem 0;
	display:flex;
	flex-direction:column;
	justify-content:center;
	align-content:center;
}
.card-intro {
	&__title__img {
		height: 2rem;
		max-width:2rem;
		margin-right: 20px;
	}
}

@mixin intro2--phone-hz() {
	.card-intro {
		min-height:180px;
	}
}

