.gallery {
	background:color(primary);
	display:flex;
	flex-direction:column;
	align-items:center;
	padding: 1rem;

}
.ba-pair-container{
	display:flex;
	justify-content:center;
	flex-wrap:wrap;

}
.ba-pair {
	display:flex;
	justify-content:center;
	margin:1rem;
	display:flex;
	flex-direction:row;
	flex-wrap:nowrap;
	width:100%;
}
.ba-img-div {
	background:color(secondary);
	&__img {
		width:100%;
	}
}
@mixin before-after--tablet() {
	.ba-pair-container {
	flex-wrap:nowrap;
	}
	.ba-pair {
		max-width:500px;
	}
	
}
