/*------------------------ Header -------------------------*/
@import "config";
#hero {
	background: color(dark);
	width:100%;
}
.container--hero {
	width:100%;
	color:black;
	display:flex;
	flex-direction:column;
	background: url('/assets/images/carpet-banner.jpg');
clip-path: polygon(100% 0, 100% 95%, 80% 100%, 0 95%, 0 0);}
.wrapper--hero-brand {
	display:flex;
	flex-direction:column;
	justify-content:center;
	align-items:center;
	&__text {
		background: hsla(0,0,0,.7);
		margin: 6rem 0 0 0;
		padding:1rem 8rem 1rem 8rem;
		display:flex;
		flex-direction:column;
		justify-content:center;
		align-items:center;
	}
}
.hero-title {
	text-shadow: 3px 4px 5px black;
	white-space:nowrap;
	color:color(secondary);
	font-size:4rem;
	font-family:'charm', cursive;
	margin: 0 0 1rem 0;
}
.hero-subtitle{
	font-family:'charm', cursive;
	color:color(secondary);
	white-space:nowrap;
	width:100%;
	font-size:2rem;
	text-shadow: 3px 4px 5px black;
}
.wrapper--cta{
	display:flex;
	flex-direction:column;
	justify-content: flex-start;
	align-items:center;
	padding: 1rem;
	margin: 80px 0 0 0;
	background: hsla(44,100,50,.5);
	background:none;
	position:relative;
}
.wrapper--cta::before{
	z-index:-1;
	content:"";
	width:50rem;
	height:50rem;
	position:absolute;
	left:50%;
	transform:translate(-30%,-40%);
	background: hsla(44,100,50,.3);
	clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
}

.buttons-wrapper {
	display:flex;
	flex-direction:column;
	align-items:center;
	margin: 3rem 0 5rem 0;
}
@mixin banner--phone-hz() {
	.container--hero {
	clip-path: polygon(100% 0, 100% 90%, 82% 100%, 0 90%, 0 0);}
}
@mixin banner--tablet() {
	.container--hero {
	clip-path: polygon(100% 0, 100% 90%, 82% 100%, 0 90%, 0 0);}
	.wrapper--hero-brand__text {
		margin: 8rem 0 0 0;
	}
}
@mixin banner--desktop(){
	#hero {
		width:100%;
	}
	.container--hero {
		display: flex;
		flex-direction: row;
		clip-path: polygon(100% 0, 100% 80%, 80% 100%, 0 80%, 0 0);overflow:hidden;
		justify-content:space-around;
	}
	.wrapper--hero {
		width: 500px;
		justify-content: center;
		align-items:center;
		padding: 0 1rem 0 1rem;
	}
	.wrapper--hero-brand__text {
		margin: 10rem 0 10rem 0;
	}
	.wrapper--cta {
		margin: 10rem 0 0 0;
	}
.wrapper--cta::before{
	width:50rem;
	height:50rem;
	left:50%;
	transform:translate(-50%,-35%);
}
}
