@import "config";
@import "core";
@import "general";

.navbar {
	z-index:1010;
	width:100%;
	background: color(primary); 
	height:5rem;
	position: fixed;
	top:0;
	right:0;
	left:0;
	max-width: $page-maxwidth;
	margin: 0 auto;
	@extend .row;
	&__logo {
		display:none;
	}
	span {
		display:block;
		content:'';
		width: 10px;
		height: 1px;
		background: color(white);
	}
	&__list {
		z-index:1010;
		width: 100%;
		position:absolute;
		top:0;
		left: 0;
		height:100vh;
		background: color(primary);
		padding:0;
		margin: 0;
		list-style:none;
		@extend .col;	
		justify-content: space-around;
		align-items: center;
		transform-origin: right;
		transform: scale(0, 1);
		transition: transform 400ms ease-in-out;

		li {
			a {
				display:block;
				padding: 2rem;
				color: color(secondary);
				opacity:0;
			}
			a:hover {
				color:color(primary);
				background: color(secondary);
				transition: all 200ms ease-in-out;	
			}
			a.current {
				text-decoration: underline;
				text-underline-offset: .25rem;	
			}
		}
	}
}
.navbar__toggle {
	display: none;
}
.navbar__toggle:checked ~ .navbar-toggle-label {
	background:color(secondary);
}
.navbar__toggle:checked ~ .navbar__list {
	transform: scale(1,1);
	li a {
		opacity: 1;
		transition: opacity 400ms ease-in-out 200ms;
	}
}
.navbar-toggle-label {
	z-index: 1011;
	color:white;
	position: absolute;
	left: 1rem;
	height: 100%;
	display:flex;
	align-items: center;
	background: color(grey-accent);
	border-radius:2px;
	padding: .5rem;
	height: 2rem;
	margin: .5rem 0 0 0;
	transition: padding 300ms ease-in-out;
}
.navbar-toggle-label span,
.navbar-toggle-label span::before,
.navbar-toggle-label span::after {
	display:block;
	background: color(white);
	border-radius: 2px;
	height: 2px;
	width: 30px;
	position:relative;
}
.navbar-toggle-label span::before,
.navbar-toggle-label span::after {
	content: '';
	position: absolute;
}
.navbar-toggle-label span::before {
	bottom:	7px;
}
.navbar-toggle-label span::after {
	top:7px;
}
.call-btn {
	display:flex;
	justify-content: center;
	align-items: center;
	flex-wrap: no-wrap;
	padding: .25rem;
	position: absolute;
	right:0px;
	&__img {
		margin: .5rem .5rem 0 .5rem;
		width:30px;
		padding:.5rem;
		background: color(white);
		clip-path: circle(50%);	
		transition: background 150ms ease-in-out;
	}
	&__img:hover {
		background: color(odd);
	}
	&__text {
			white-space:nowrap;
			text-align:center;
		color: color(primary);
		display: flex;
		justify-content: center;
		background: color(white);
		margin: .5rem .5rem 0 .5rem;
		padding:1rem 2rem;
		border-radius:2px;
		font-weight: bold;
		white-space:no-wrap;
		transition: all 150ms ease-in-out;
		&__phone {
		}
		&__desktop {
			display:none;
		}
	}
	&__text:hover {
		background: color(odd);
		color: color(primary);
	}
}
@mixin nav--phone-hz() {
}
@mixin nav--desktop() {
	.navbar {
		position:fixed;
		display:flex;
		flex-direction:row;
		justify-content: space-around;
		&__logo {
			display: block;
			background:none;
			display:flex;
			flex-direction:column;
			justify-content:center;
			img {
				padding: .25rem 1rem .25rem 4rem;
				height: 3rem;
			}
		}
		&__list {
			background: none;
			transform:none;
			position:static;
			box-shadow:none;
			backdrop-filter:none;
			display:flex;
			flex-direction:row;
			justify-content:space-evenly;
			height: auto;
			li a {
				opacity:1;
			}
		}
		&-toggle-label {
			display:none;
		}
	}
	.call-btn {
		position:static;
		margin: 0 5rem 0 0;
		&__text {
			&__phone {
				display:none;
			}
			&__desktop {
				display:block;
			}
		}
		&__img {
			display:none;
		}
	}
}
