@import "config";
@import "core";
@import "general";

.block-top-border-testimonials {
	@extend .row;
	justify-content:center;
	background:color(accent-primary);
	height:100px;
	width: 100%;
	clip-path: polygon(50% 0, 100% 20%, 100% 80%, 50% 100%, 0 80%, 0 20%);
}
.testimonials-header {
	max-width: 300px;
	margin: auto;
	transform: translate(0, -5px);
}
.wrapper-testimonials-all {
	padding: 4rem 0 4rem 0;
	justify-content: center;
	align-items:center;
}
.container-top-border {
	max-width: 55ch;
	background-image: linear-gradient(45deg, color(secondary), color(tetriary) );
	border: 1px solid color(secondary);
	border-radius: 8px;
	padding-top:.5rem;
	margin: 1rem 1rem 2rem 1rem;
}
.testimonials-card {
	@extend .col;
	background: color(white);
	padding: 1rem 2rem 2rem 2rem;
	border: none;
	border-radius: 8px;
	min-height:16rem;
	&__title {
		@extend .row;
		color:color(primary);
		img {
			display:block;
			width: 60px;
			clip-path: circle(30px);
			margin: 0 2rem 0 0 ;
		}
	}
	&__text {
		margin: 1rem 0 0 0;
		font-family:serif;
		font-style: italic;
		color: color(grey-dark);
		line-height: 1.5;
	}
	&__date {
		margin: 1rem 0 0 0;
		color: color(grey-accent);
		font-family:serif;
		font-style: italic;
		align-self:flex-end;
	}
}

@mixin testimonials--phone-hz() {
	.container-top-border {
	box-shadow:10px 10px 8px #888888; 
	border: none;
	border-left: 1px solid color(secondary);
	}
}
@mixin testimonials--tablet() {
.container-testimonials{
	border-top:1px solid color(accent-primary); 
}
.block-top-border-testimonials {
	width:100%;
	clip-path:ellipse(35% 100% at 50% 0%);
}
}
