@import "normalize";
@import "core";
@import "general";

@import "nav";
@import "banner";
@import "intro2";
@import "testimonials";
@import "services";
@import "before-after";
@import "pitch";
@import "portfolio";
@import "mini-about2";
@import "pricing";
@import "bottom-form";
@import "footer2";

@import "banner-subpage";
@import "subpage";
@import "about";
@import "subpage-contact-button";
@import "bottom-form-2col";

/* phone-small is default */
$phone-big: 567px;
$phone-hz:567px; /*keep? */
$tablet:768px;
$desktop: 992px;
$l--desktop: 1200px;
$xl--desktop: 1800px;

@media (min-width: #{$phone-hz}) {
	@include banner--phone-hz()
	@include nav--phone-hz()
	@include general--phone-hz()
	@include intro2--phone-hz()
}

@media (min-width: #{$tablet}) {
	@include core--tablet()
	@include general--tablet()
	@include banner--tablet()
	@include services--tablet()
	@include before-after--tablet()
	@include pitch--tablet()
	@include mini-about2--tablet()
	@include pricing--tablet()
	@include footer2--tablet()
	@include subpage--tablet()
	@include banner-subpage--tablet()

}
@media (min-width: #{$desktop}) {
	@include nav--desktop()
	@include banner--desktop()
	@include portfolio--desktop()
	@include pricing--desktop()
	@include bottom-form-2col--desktop()
	@include bottom-form--desktop()
	@include general--desktop()
	@include footer2--desktop()
	@include subpage--desktop()
	@include subpage-contact-button--desktop()
	@include about--desktop()
}
@media (min-width: #{$l--desktop}) {
	@include subpage--l--desktop()
}

