$backgroundColor: #212121;
$bodyColor: #000000;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

$colors: (
	dark:hsla(40, 0, 11, 1),
	primary:hsla(40, 0, 11, 1),
	secondary: hsla(54,83,64,1),
	tetriary: hsla(54,83,54,1),
	golda: hsla(36, 45, 42, 1),
	goldb: hsla(55, 84, 80, 1),
	accent:hsla(114, 60, 60, 1),
	odd: hsla(56, 100, 66,1),
	accent-primary: hsla(210,94,10,.7),
	very-light: hsla(210,94,96,1),
	error: red,
	grey-dark: #212121,
	grey-light: #333333,
	grey-accent: #636363,
	neutral: #212121,
	white: #fff,
	btnphover: hsla(5,80,50,1),
	btnoddhover: hsla(56, 100, 50,1),
	btnaccenthover:hsla(114, 60, 50, 1),
	orange: #D65800,
);
$font-sizes: (
	h1: 2rem,
	h2: 1.5rem,
	h3: 1.25rem,
	text: 1rem,
	button: 1.25rem,
);
$page-maxwidth: 1910px;
@function color($color-name) {
	@return map-get($colors, $color-name);
};

@function font-size($font-name) {
	@return map-get($font-sizes, $font-name);
};

