.container--bottom-form {
	width:100%;
	display:flex;
	justify-content: center;
	flex-wrap: nowrap;
	background: url('/assets/images/carpet-banner.jpg');
	background-size:cover;
	background-position: center;
	background-repeat: no-repeat;
	font-family:serif;
	overflow: hidden;
	text-shadow: 1px 1px black;
}

.form-wrapper {
	padding: 5rem 1rem 5rem 1rem;
	displpay:flex;
	flex-direction:column;
	max-width: 400px;
	flex:2;
	background:hsla(1,1,1,.5);
	form {
		margin: 1rem 0 0 0;
		border-radius: 5px;
		input, textarea {
			width:80%;
			border: none;
			padding: 1rem;
			margin-bottom: 1rem;
			font-size: font-size(text);
		}
		label {
			color: color(white);
			display: block;
			width: 100%;
			padding: 5px;
			font-weight: bold;
			font-size: font-size(text);
		}
	}
}
.form__header {
			padding:0;
		margin: 0;
		color: color(white);
		font-family:serif;
		font-weight:bold;
}
.form__header-1 {
font-size: font-size(h1);
}
.form__header-2 {
font-size: font-size(h2);
}

@mixin bottom-form--desktop(){
	.container--bottom-form {
	background-attachment:fixed;
}
	.form-wrapper {
		margin: 0 auto;
	}
}
