
.wrapper-portfolio-cards {
	display:flex;
	flex-direction: column;
	align-items:center;

}
.portfolio__card {
	max-width: 300px;
	border: 1px solid rgba(0,0,255,0.1);
	border-top:4px solid color(primary);
	padding: 1rem;
	margin: 1rem;
	height:450px;
	display:flex;
	flex-direction: column;
	justify-content:space-between;
	box-shadow: 12px 12px 12px 1px rgba(0,0,255,0.2);
	&__wrapper {
		flex-direction: column;
		justify-content:flex-start;
	}
	&__img {
		width:80px;
		margin: 0 0 1rem 0;
	}
	&__title {
		font-size:1.5rem;
		color:color(grey-dark);
		font-family: 'Cairo', sans-serif;
	}
	&__sub-title {
		color:color(grey-accent);
		font-weight:bold;
		margin: 1rem 0 1rem 0;
	}
	&__text {
		margin: 1rem 0 1rem 0;
		color:color(grey-accent);
	}
	&__button {
		bottom:0;
		width:100%;
		display:flex;
		justify-content:center;
	}
}
@mixin portfolio--desktop() {
.wrapper-portfolio-cards {
	flex-direction: row;
	align-items:center;
	justify-content:center;

}

}
