#pricing {
	background: linear-gradient(color(white),color(accent));
}
.h1-card {
}
.container--pricing {

	padding: 0 0 10rem;
}
.pricing__wrapper-cards {
	display:flex;
	flex-direction:column;
	align-items:center;
}
.pricing__card {
	background:white;
	min-width: 280px;
	margin: 1rem 0 1rem 0;
	height:450px;
	display:flex;
	flex-direction: column;
	justify-content:space-between;
	align-items:center;
	color:color(grey-light);
	text-align: center;
	&__banner {
		background:color(accent);
	padding: 1rem 0;
	width:100%;
	font-size:1.5rem;
	font-family:'Cairo', sans-serif;
	text-align: center;
	}
	&__title {
		padding: 1rem 0 0 0;
	}
	&__price {
		font-size: 4rem;
	font-family:'Cairo', sans-serif;
	}
	&__list {
		display:flex;
		flex-direction:column;
		align-items:flex-start;
	}
	&__li {
		&__img {
			display:inline-block;
			height: 24px;
			width: 24px;
			transform: translate(0, 6px);
		}
	}


}
@mixin pricing--tablet() {
	.pricing__wrapper-cards {
	flex-direction:row;
	flex-wrap:no-wrap;
	justify-content:center;
	}
	.pricing__card {
		min-width:250px;
		margin:.25rem;
	}
.pricing__card-middle {
	transform:scale(1.1);
	z-index:100;
	box-shadow: 0 0 25px -10px black;
}
.pricing__card-side {
z-index:90;
}

}
@mixin pricing--desktop() {
	.pricing__card {
		min-width:300px;
		margin:.25rem;
	}
}
