@import "config";

body {
	background: $backgroundColor;
	color: $bodyColor;
	font-family: $bodyFont;
}

.container--main--bg {
	background: color(white);
}
.col {
	display: flex;
	flex-direction: column;
}
.row {
	display: flex;
	flex-direction: row;
}
.container {
	width: 100%;
}

.wrapper-col-to-row-at-d{
	@extend .col;
}
.wrapper-col-to-row-at-t{
	@extend .col;
}
.img-blk {
	display: block;
	width: 100%;
}
p {
	max-width: 55ch;
}
@mixin core--tablet() {
	.wrapper-col-to-row-at-t{
		display:flex;
		flex-direction:row;
		flex-wrap:wrap;
	}
}
@mixin core--desktop() {
	.wrapper-col-to-row-at-d{
		display:flex;
		flex-direction:row;
		flex-wrap:wrap;
		justify-content:space-between
	}
}

