@import "config";
@import "core";
@import "general";

.mini-about2__wrapper-all {
	display:flex;
	flex-wrap:wrap;
	align-items:center;
	justify-content:center;
	padding: 0rem 1rem 5rem 1rem;
}
.mini-about2__wrapper-img {
	height:300px;
	width:300px;
	display:flex;
	flex-direction:column;
	align-items:center;
	&__img {
		display:block;
	clip-path: circle(50%); 
	height:200px;
	width:200px;
	margin: 0 0 2rem 0;
}
}
.mini-about2__wrapper-img::before {
	content: '';
	background:url('/assets/images/blob.svg');
	background-repeat:no-repeat;
	background-position:center;
	background-size:contain;
	position:absolute;
	height: 320px;
	width: 320px;
transform:translate(0,-60px);
}
.about2__img-title {
	padding:1rem 0 0 0;
	color: color(secondary);
	font-weight:bold;

}
.mini-about2-card {
	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	padding: 1rem;
	max-width: 55ch;
	line-height: 2;
	&__title {
		@extend .col;
		/*align-items:center;*/
		font-weight:bold;
		font-size:font-size(h1);
		color: color(grey-accent);

	}
	&__text {
		font-family:serif;
		margin: 0 0 4rem 0;
	}
}
.text-highlight {
	color: color(tetriary);
	font-weight: bold;
	font-style: serif;
}
@mixin mini-about2--tablet() {
	.mini-about2-card {
		padding: 1rem 1rem 1rem 5rem;
	}
}
