.about__wrapper-all {
	flex-wrap:wrap;
	align-items:flex-start;
	justify-content:center;
	padding: 5rem 1rem 5rem 1rem;
}
.about-img {
	border-radius:.5rem;
    box-shadow: 12px 12px 12px 1px grey;
}
.about-server-img {
	max-width:55ch;
}
.about__row {
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
	justify-content:center;
	padding: 2rem 0;
}
.about__col {
	display:flex;
	flex-direction:col;
	align-items:center;
	justify-content:center;
}
.about-title {
	font-weight: bold;
	font-size: 2rem;
	line-height:2;
	color: color(grey-accent);
	display:flex;
	justify-content:center;
}
.about-text {
max-width: 80ch;
font-family:"Comfortaa", sans-serif;
line-height:2;
color:color(grey-dark);
}
.about-text,.about-card__text {
max-width: 80ch;
font-family:"Comfortaa", sans-serif;
line-height:2;
color:color(grey-dark);
}
.about-wrapper {
	margin: 0 1rem;
}
.about-card__title {
	&__img {
	height:220px;
	max-width: 220px;
}
&__text {
	width:100%;
	display:flex;
	justify-content:left;
}
}

@mixin about--desktop(){
	.about__order2-ph {
		order:2;
	}
	.about__order1-ph {
		order:1;
	}
}
