.container--bottom-form-2col {
	width:100%;
	display:flex;
	flex-direction:row;
	justify-content: space-evenly;
	flex-wrap: wrap;
	background: color(white);
	background-size:cover;
	background-position: center;
	background-repeat: no-repeat;
	overflow: hidden;
padding: 4rem 0;
}
.form-wrapper-2col {
	padding: 5rem 1rem 5rem 1rem;
	display:flex;
	flex-direction:column;
	max-width: 400px;
	box-shadow: 12px 12px 12px 1px rgba(0, 0, 255, .2);
	border: 1px solid rgba(0, 0, 255,.1);
	h2 {
		padding:0;
		margin: 0;
		color: color(grey-accent);
	}
	form {
	min-width:280px;
		margin: 1rem 0 0 0;
		border-radius: 5px;
		input, textarea {
			width:80%;
			border: none;
			padding: 1rem;
			margin-bottom: 1rem;
			background: #f2f7f3;
			font-size: font-size(text);
		}
		label {
			color: color(secondary);
			display: block;
			width: 100%;
			padding: .25rem 0 .25rem 0;
			font-weight: bold;
			font-size: font-size(text);
		}
	}
}
.form-wrapper-info-2col {
	display:flex;
	flex-direction:column;
	justify-content:space-between;
	width: 280px;
	padding: 5rem 1rem 5rem 1rem;
	max-width: 400px;
	h2 {
		padding:0;
		margin: 0 0 1rem 0;
		color: color(grey-accent);
	}
}
.form-info-block {
	color: color(grey-accent);
}

@mixin bottom-form-2col--desktop(){
	.container--bottom-form-2col {
}
}


