@import "config";
#banner-subpage {
	padding: 80px 0 0 0;
}
.subpage__header {
	width: 100%;
	background: color(white);
}
.subpage__header__wrapper {
	padding: 1rem;
	&__text {
		display:inline-block;
		color: color(secondary);
		font-size: font-size(h1);
		font-family: 'Comfortaa', sans-serif;
	}
}
.location {
	padding: 1rem;
	color: color(grey-accent);
	font-weight: bold;
	font-family: 'Cairo', sans-serif;
	font-size:1.5rem;
	a {
		color: color(secondary);
		text-decoration: underline;
	}
}

@mixin banner-subpage--tablet() {
	#banner-subpage {
		padding-left:5rem;
	}
	}
