
.container-pitch {
	padding:1rem;
	display:flex;
	flex-direction:column;
	align-items:center;
}
.wrapper-pitch {
	&__img {
		display:inline-block;
		width:300px;
		height:300px;
	}
	&__text {
		max-width:55ch;
		color: color(grey-accent);
		line-height:2;
	}
}
@mixin pitch--tablet() {
	.wrapper-pitch {
	display:flex;
	flex-wrap:wrap;
	align-items:center;
	}
}
