@import "config";
.services {
	&__container {
		justify-content:center;
	}
	&__wrapper-2cards {
		display:flex;
		flex-direction:column;
		padding: 0 1rem 0 1rem;
		align-items:center;
		background: color(primary);
	}
	&__card {
		width:25rem;
		height:15rem;
		background:blue;
		margin: 0.5rem 0 0.5rem 0;
		display:flex;
		flex-direction:column;
		justify-content:flex-start;
		position:relative;

	}
	&__card__title {
		font-size:2rem;
		font-family:'Charm',cursive;
		color: color(primary);
		background:hsla(50,0,100,.6);
		display:flex;
		justify-content:center;
		padding:1rem;

	}
	&__ard::before {
		content:"before title";
		position:absolute;
		bottom:50%;
		left:50%;
		transform:translate(-50%,-50%);
	}
}

@mixin services--tablet() {
	.services {
		&__wrapper-2cards {
		flex-direction:row;
		justify-content:center;
	}
		&__card {
			margin:1rem;

		}
	}
}
