footer {
	max-width: $page-maxwidth;
	margin: 0 auto;
}
.footer-container {
	display:flex;
	flex-direction:column;
	align-items:center;
	flex-wrap: nowrap;
	padding: 2rem 0 2rem 0;
	color: color(secondary);
}
.footer-wrapper-all{
	display:flex;
	flex-direction:column;
}
/*---------------- Logo ---------------------*/
.footer-brand-wrapper {
	display:flex;
	flex-direction:row;
	width: 240px;
	margin: 0 0 0.5rem 0;
	img {
		height:90%
	}
}
.footer-logo-wrapper {
	height:100%;
}
.band-name-wrapper {
	display:flex;
	flex-direction:column;
	align-items:flex-end;
}
/*---------------- ---------------------*/
.footer-block {
	padding: 1rem;
	display:flex;
	flex-direction:column;
	
	justify-content:right;
}
.footer-wrapper {

}
.footer-titles__wrapper {
	display:flex;
	flex-direction:column;
	
	justify-content: left;
	font-family:sans-serif;
}
.footer-titles__title {
	font-style: sans-serif;
	font-size: 1.5rem;
	font-weight: bold;
	color: color(grey-accent);
}
.footer-titles__divider {
	width: 30px;
	height: 1px;
	background: color(grey-accent);
	margin:.5rem 0 1rem 0;
}

/*---------------- Contact ---------------------*/
#footer-contact {
	@extend .footer-wrapper;
	a {
		color: color(white);
		font-weight: bold;	
	}
}
.footer-contact__content {
	
	display:flex;
	flex-direction:column;
	justify-content: left;
	&__item {
		padding: .5rem .5rem .5rem 0;
		line-height: 1.5;
		color:color(secondary);
		text-decoration: underline;
		text-underline-offset: .25rem;
		img {
			height: 1.75rem;
			transform: translate(0, .5rem);
			padding: 0 .5rem 0 0;
		}
	}
	&__item:hover {
		color: color(tetriary);
		text-underline-offset: .2rem;
		transition: color 200ms ease-in-out;
		border-radius:2px;
	}
}
/*---------------- Links ---------------------*/
.footer-links-wrapper2 {
	
	display:flex;
	flex-direction:column;
	flex-wrap: wrap;
	justify-content: space-between;
}
.footer-link {
	font-family: sans-serif;
	color: color(secondary);
	font-weight:bold;
	text-decoration:underline;
	text-underline-offset: .25rem;
	margin: .5rem 0 .5rem 0;
		padding: .5rem 0;
}
.footer-link:hover {
	color: color(tetriary);
		border-radius:2px;
}
/*---------------- Copyright ---------------------*/
.footer-copyright {
	
	display:flex;
	flex-direction:column;
	justify-content:center;
	align-items:center;
	height: 100px;
	background: black;
	&__text {
		color:color(secondary);
		padding:.5rem;
		font-size: .75rem;
	}
}
@mixin footer2--tablet(){
	.footer-copyright{
		height:auto;
	}
}
@mixin footer2--desktop() {
	.footer-wrapper-all {
		width: 100%;
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		justify-content: space-around;
	}
}

