@import "config";
@import "general";
@import "core";


/*------------------------ Header -------------------------*/
.banner-about{
	background: black url('/assets/images/redhouse1.jpg');
}
.banner-contact {
	background: black url('/assets/images/redhouse1.jpg');
}

.banner-subpage {
	width: 100%;
	background-color:color(white);
	&__container {
		width:100%;
		display:flex;
		flex-direction:column;
		justify-content:center;
		align-items:center;
		align-content:center;
		clip-path: polygon(50% 100%, 100% 90%, 100% 0, 0 0, 0 90%);
		background-size: cover;
		background-position:center;
		background-repeat:no-repeat;
		padding:100px 0 0 0;
	}
	&__content {
		text-align:center;
		margin: 4rem;
		font-size: 4rem;
		font-weight:bold;
		width: 250px;
		position:relative;
		z-index:100;
		color: color(white);
	}
	&__content::before {
		content: '';
		width: 150px;
		height: 150px;
		border: 10px solid rgba(255,218,45,.4);;
		position:absolute;
		left: 50%;
		transform:translate(-50%,0);
		z-index:-1;

	}
}
/*------------------------ About Only-------------------------*/
.container-about {
	background: url('/assets/images/blob.svg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}
.wrapper-image {
	display:flex;
	flex-direction: col;
	justify-content:center;
	align-items:center;
	padding: 1rem;
	z-index:2;
	img {
		display:block;
		max-width:200px;
		z-index: -1;
	}
}
.wrapper-about {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	padding: 1rem 0 4rem 0;
}
/*------------------------ Services Only-------------------------*/

.container--services {
	display: flex;
	flex-direction: column;
	width: 100%;
	justify-content:center;
	align-items:center;
	padding: 4rem 0;
	position:relative;
}
.wrapper-services-2blocks {
	display: flex;
	flex-direction: column;
	width:100%;
	z-index:100;

}
.container--services::after {
	position:absolute;
	background: url('/assets/images/jigsaw.jpg');
	background-position:center 90%;
	background-attachment:fixed;
	content:'';
	width:100%;
	height:50%;
	z-index:1;
}
.wrapper-services--img {
	overflow:hidden;
	height:250px;
	padding:1rem;
}
.img-design {
	background: url('/assets/images/services-design.jpg') no-repeat;
	background-position:center;
	background-size:cover;
	filter: brightness(50%);
}
.img-seo {
	background: url('/assets/images/services-seo.jpg');
	background-position:center;
	background-size:cover;
}
.img-branding {
	background: url('/assets/images/services-stampmaker.jpg');
	background-position:center;
	background-size:cover;
}
.img-downspout {
	background: url('/assets/images/downspout.jpg');
	background-position:center;
	background-size:cover;
}
.wrapper-services--info{
	border: 1px solid color(grey-accent);
	padding:1rem;
	background:color(white);
	&__header {
	font-size: font-size(h2);
	font-weight:bold;
	color: color(secondary);
	margin-bottom: 1rem;
	}
	&__text {
		color: color(grey-light);
		line-height: 1.5;
	}
}
.header-services {
}
/*------------------------ Content -------------------------*/
.subpage__container {
	@extend .col;
	align-items:center;
}
.subpage__text {
	background: rgba(0,0,0,.1);
	border-radius: 5px;
	padding: 1rem;
	max-width: 300px;
	color: color(grey-light);
	line-height: 1.5;
	font-size: 1.25rem;
	h3 {
		color:color(tetriary);
		margin: 0;
	}
}
/*------------------------ Mixin -------------------------*/
@mixin subpage--phone-hz() {
	.card-services{
		min-height: 300px;
	}
}
@mixin subpage--tablet() {
	.wrapper-services-2blocks{
	flex-direction:row;
	align-items:stretch;
	max-width:750px;
	background:green;
	min-height: 50ch;
	}
	.wrapper-services--img {
		height:auto;
	flex-basis:50%;
	}
	.wrapper-services--info {
	flex-basis:50%;
	}
	.order-1 {
		order:1;
	}
}
@mixin subpage--desktop() {
	.banner-subpage__container {
		background-attachment:fixed;
		clip-path: polygon(50% 100%, 100% 80%, 100% 0, 0 0, 0 80%);
	}
	.wrapper-services-2blocks{
	flex-direction:row;
	align-items:stretch;
	max-width:980px;
		background:blue;
	min-height: 40ch;
	}
	.wrapper-services--info, .wrapper-services--img {
	flex-basis:50%;
	}
}
@mixin subpage--l--desktop() {
	.wrapper-services-2blocks{
	flex-direction:row;
	align-items:stretch;
	max-width:1100px;
		background:blue;
	min-height: 35ch;
	}

}
