.color--primary {
	color: color(primary);
}
.color--secondary {
	color: color(secondary);
}
.color--tetriary {
	color: color(tetriary);
}
.color--accent {
	color: color(accent);
}
.color--grey-light{
	color: color(grey-light);
}
.color--grey-accent{
	color: color(grey-accent);
}

.color--white{
	color: color(white);
}
.bg--secondary {
	background: color(secondary);
}
h1 {
	font-size: font-size(h1);
}
h2 {
	font-size: font-size(h2);
}
h3 {
	font-size: font-size(h3);
	line-height:1.5;
}
a {
	text-decoration: none;
	color: none;
}
main {
	max-width: $page-maxwidth;
	margin: 0 auto;
	z-index:1;
	font-family:'Comfortaa',sans-serif;
}
.hidden {
	display:none;
}
.wrapper-col {
	width:100%;
	display:flex;
	flex-direction:column;
	align-items:center;
}
.wrapper-row {
	display: block;
}
/*------------------------  cards -------------------------*/
.wrapper-card-all {
	display:flex;
	flex-wrap:wrap;
	padding: 4rem 1rem 4rem 1rem;
}

.h1-card {
	color: color(grey-light);
	padding: 5rem 0 0 0;
	margin:0 auto;
}
.card {
	display:flex;
	flex-direction:column;
	color: color(grey-dark);
	max-width: 320px;
	padding: 0 0 2rem 0;
	&__itle {
		display:flex;
	}
	&__img {
		width:100%;
		height: auto;
	}
	&__text {
		color: color(grey-dark);
		margin-bottom: 20px;
		font-size: font-size(text);
		line-height: 2;
	}
}
.card__title-w-inline-img {
	display:flex;
	flex-wrap:no-wrap;
	align-items:center;
	color:color(primary);
	&__img {
		display:block;
		width:40px;
		height:40px;
		margin: .25rem;
	}
}
/*------------------------ 3 cards -------------------------*/
.wrapper-3card-all {
	display:flex;
	flex-direction:column;
	justify-content:space-around;
	align-items:center;
	padding: 1rem 1rem 1rem 1rem;
}
.card-3 {
	max-width: 320px;
	&__title {
		display:flex;
		flex-direction:column;
		align-items: center;
		color: color(secondary);
	}
	flex:1;
}
/*------------------------ 4 cards -------------------------*/
.wrapper-4card-all {
	justify-content:center;
}
.4card-card {
	max-width: 320px;
	&__img {
		width:100%;
		height: auto;
	}
	__title {
		display:flex;
		flex-direction:row;
		justify-content: flex-start;
		align-items: center;
		color: color(secondary);
	}
}
/*------------------------ Buttons -------------------------*/
.btn {
	font-size: font-size(button);
	border: none;
	border-radius: 3px;
	width: 200px;
	height: 50px;
	padding: 12px 22px 12px 22px;
	margin-bottom: 20px;
}
.btn--odd {
	background: color(odd);
	color: color(grey-dark);
}
.btn--opaque {
	border: 1px solid color(white);
	background: rgba(0,0,0,.7);
	color: white;
	text-shadow: 2px 2px 5px, black;
}
.btn--primary{
	border: none;
	background: color(primary);
	color: white;
}
.btn--accent {
	border: none;
	background: color(accent);
	color: white;
	font-weight:bold;
		text-shadow:1px 1px #737373;
}
.btn--primary:hover {
	background: color(white);
	color: color(primary);
	border: 2px solid color(primary);
}
.btn--opaque:hover {
	background: rgba(0,0,0,.8);
	color: white;
}
.btn--odd:hover {
	background: color(btnoddhover);
	font-weight:bold;
	transition: all 200ms ease-in-out;
}
.btn--accent:hover {
	background: color(btnaccenthover);
	font-weight:bold;
}

.header-divider {
	display:flex;
	justify-content:center;
	&__img {
		transform: translate(0,15px);
		display:block;
		height:50px;
	}
}
.dashed {
	display:block;
	width: 50px;
	border-bottom: 1px dashed color(secondary);
}

@mixin general--phone-hz() {
	.wrapper-4card-two {
		padding: 0 2rem 0 2rem;
	}
}
@mixin general--tablet() {

}
@mixin general--desktop () {
	main {
		/*	margin: 800px auto 0 auto;*/
	}
	.fixed-title-subpage {
		display:none;
	}
	.wrapper-row {
		display: flex;
		flex-wrap:wrap;
	}
	.card-4card {
	}
	.wrapper-4card-all {
		justify-content:space-evenly;
	}
	.wrapper-3card-all {
		display:flex;
		flex-direction:row;
		align-items:flex-start;
	}
	.card {
		&__text {
		}
	}

}

